@charset "UTF-8";
/* Файл global/fonts.scss */
html {
  height: 100%;
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  margin: 0;
  height: 100%;
  background-color: white;
  font-style: normal;
  font-weight: 400;
  font-family: monospace; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

